import {
	basePath
} from '@/utils/index'
import {
	mapState
} from 'vuex'
import tuiSwipeAction from "@/components/swipe-action/swipe-action"
import tuiButton from "@/components/extend/button/button"
import tuiNumberbox from "@/components/numberbox/numberbox"
import tuiIcon from "@/components/icon/icon"
import tuiDivider from "@/components/divider/divider"
import tuiLoadmore from "@/components/loadmore/loadmore"
export default {
	components: {
		tuiSwipeAction,
		tuiButton,
		tuiNumberbox,
		tuiIcon,
		tuiDivider,
		tuiLoadmore
	},
	data() {
		return {
			basePath,
			actions: [
				// {
				// 	name: '收藏',
				// 	width: 64,
				// 	color: '#fff',
				// 	fontsize: 28,
				// 	background: '#FFC600'
				// },
				// {
				// 	name: '看相似',
				// 	color: '#fff',
				// 	fontsize: 28,
				// 	width: 64,
				// 	background: '#FF7035'
				// },
				{
					name: '删除',
					color: '#fff',
					fontsize: 28,
					width: 64,
					background: '#F82400'
				}
			],
			isEdit: false,
			productList: [],
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			allchecked: false, //全选默认false
			/* totalNum:0,//选中商品总数量*/
			totalPrice: 0, //选中商品总金额 
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'member_id']),
	onLoad() {
		let shopCartArr = uni.getStorageSync('shopCartList');
		if (shopCartArr) {
			this.productList = JSON.parse(shopCartArr);
			this.productList.forEach(item => {
				item.checked = false;
			})
			console.log(JSON.stringify(this.productList))
		}
	},
	methods: {
		countTotal() {
			let totalPrice = 0;
			this.productList.map(item => {
				console.log(111)
				item.checked ? totalPrice += item.qyt * item.price : totalPrice += 0
			})
			this.totalPrice = totalPrice
			//return totalPrice

		},
		// 单个商品前的勾选
		selected(item) {

			item.checked = !item.checked
			console.log("单选" + JSON.stringify(item.checked))
			if (!item.checked) {
				this.allchecked = false
			} else {
				const test = this.productList.every(item => {
					return item.checked === true
				})
				if (test) {
					this.allchecked = true
				} else {
					this.allchecked = false
				}
			}
			this.countTotal()

		},
		//全选
		selectedAll() {
			this.allchecked = !this.allchecked;
			if (this.allchecked) {
				this.productList.map(item => {
					item.checked = true
				})
			} else {
				this.productList.map(item => {
					item.checked = false
				})
			}
			this.countTotal()
		},
		// 删除挑选的购物车商品
		deleteGoods(item, index) {
			this.productList = this.productList.filter(item => !item.checked);
			var $me = this;
			uni.setStorage({
				key: 'shopCartList',
				data: this.productList.length > 0 ? JSON.stringify(this.productList) : '',
				success: function(res) {
					$me.tui.toast("删除成功", 2000, true);

				}
			});
			this.allchecked = false
			this.countTotal()
		},
		changeNum: function(e) {
			this.productList[e.index].qyt = e.value;
			this.countTotal()

		},
		handlerButton: function(e) {
			console.log("单选" + JSON.stringify(e))
			let index = e.index;
			let item = e.item;
			// this.tui.toast(`商品id：${item.id}，按钮index：${index}`);
			this.productList = this.productList.filter(subitem => subitem.id!=item.id);
			var $me = this;
			uni.setStorage({
				key: 'shopCartList',
				data: this.productList.length > 0 ? JSON.stringify(this.productList) : '',
				success: function(res) {
					$me.tui.toast("删除成功", 2000, true);
			
				}
			});
			this.allchecked = false
			this.countTotal()
		},
		editGoods: function() {

			this.isEdit = !this.isEdit;

		},
		detail: function() {
			uni.navigateTo({
				url: '../../productDetail/productDetail'
			})
		},
		btnPay() {
			var ids = this.productList.map(item => {
				var items = {
					id: item.id,
					qyt: item.qyt
				}
				return items
			})
			uni.navigateTo({
				url: '../submitOrder/submitOrder?ids=' + JSON.stringify(ids)
			})
		}
	},
	onPullDownRefresh: function() {
		let loadData = JSON.parse(JSON.stringify(this.productList));
		loadData = loadData.splice(0, 10)
		this.productList = loadData;
		this.pageIndex = 1;
		this.pullUpOn = true;
		this.loadding = false
		uni.stopPullDownRefresh()
	},
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		if (this.pageIndex == 4) {
			this.loadding = false;
			this.pullUpOn = false
		} else {
			let loadData = JSON.parse(JSON.stringify(this.productList));
			loadData = loadData.splice(0, 10)
			if (this.pageIndex == 1) {
				loadData = loadData.reverse();
			}
			this.productList = this.productList.concat(loadData);
			this.pageIndex = this.pageIndex + 1;
			this.loadding = false
		}
	}
}
